<template>
    <v-container>
      <v-row
        align='center'
        justify='center'
      >
        <v-col>

          <v-card
            :loading="orders_loading"
            class="mx-auto"
            max-width="100%"
            rounded
          >
            <v-list-item v-if="orders_loading" two-line>
              <v-list-item-content>
                <div class="text-overline mb-4">
                  Поръчки
                </div>
              </v-list-item-content>
              <v-list-item-avatar
                tile
                size="80"
              >
              <v-icon size="80">mdi-package-variant-closed</v-icon>
              </v-list-item-avatar>
            </v-list-item>

            <v-list-item v-else two-line>
              <v-list-item-content>
                <div class="text-overline mb-4">
                  Поръчки
                </div>
                <v-list-item-title class="text-h5 mb-1">
                  Общ брой пратки: {{orders_domestic + orders_international}}
                </v-list-item-title>
                <v-list-item-subtitle class="md-2">Вътрешни: {{orders_domestic}}</v-list-item-subtitle>
                <v-list-item-subtitle>Международни: {{orders_international}}</v-list-item-subtitle>
              </v-list-item-content>
              <v-list-item-avatar
                tile
                size="80"
              >
              <v-icon size="80">mdi-package-variant-closed</v-icon>
              </v-list-item-avatar>
            </v-list-item>
          </v-card>

        </v-col>

        <v-col>

          <v-card
           :loading="turnover_loading"
            class="mx-auto"
            max-width="100%"
            rounded
          >
          <v-list-item v-if="turnover_loading" two-line>
              <v-list-item-content>
                <div class="text-overline mb-4">
                  Оборот/Печалба
                </div>
              </v-list-item-content>
              <v-list-item-avatar
                tile
                size="80"
              >
              <v-icon size="80">mdi-cash-fast</v-icon>
              </v-list-item-avatar>
            </v-list-item>

            <v-list-item v-else two-line>
              <v-list-item-content>
                <div class="text-overline mb-4">
                  Оборот/Печалба
                </div>
                <v-list-item-title class="text-h5">
                  Печалба: {{turnover_total}}
                </v-list-item-title>
                <v-list-item-title class="text-h5 mb-1">
                  Оборот: {{(turnover_domestic + turnover_international).toFixed(2)}}
                </v-list-item-title>
                <v-list-item-subtitle>Оборот вътрешни: {{(turnover_domestic).toFixed(2)}} </v-list-item-subtitle>
                <v-list-item-subtitle>Оборот външни: {{(turnover_international).toFixed(2)}}</v-list-item-subtitle>
              </v-list-item-content>

              <v-list-item-avatar
                tile
                size="80"
              >
              <v-icon size="80">mdi-cash-fast</v-icon>
              </v-list-item-avatar>
            </v-list-item>


          </v-card>

        </v-col>


      </v-row>
    </v-container>

</template>

<script>
import axios from 'axios'
import { mapActions } from 'vuex'

  export default {
    name: 'AdminHomeDashboard',

    components: {
    },

    data: () => ({
        headers: [
          {
            text: 'Access Event',
            align: 'start',
            filterable: false,
            value: 'event',
          },
          { text: 'Accessed Room', value: 'room' },
          { text: 'User', value: 'user' },
          // { text: 'Biometric ID', value: '_bio_id' },
          { text: 'Time', value: 'time' },
          { text: 'Date', value: 'date' },
        ],
        // Loading Segments
        orders_loading: true,
        turnover_loading: true,

        // Data
        orders_domestic: 0,
        orders_international: 0,
        turnover_domestic: 0,
        turnover_international: 0,
    }),

    computed: {
      turnover_total() {
        return (parseFloat(this.turnover_domestic + this.turnover_international)*0.1).toFixed(2)
      }
      // organization_users() {
      //   const count = this.$store.getters.stateUser.current_users + '/' + this.$store.getters.stateUser.max_users
      //   return count
      // },
    },

    methods: {
      ...mapActions(['viewMe']),
      async getUserData(){
        await this.viewMe()
      },

      async getParcelData(){
        const dataDB = await axios.get('/admin/home')
        console.log(dataDB);
        this.orders_domestic = dataDB.data.orders_domestic
        this.orders_international = dataDB.data.orders_international
        this.turnover_domestic = dataDB.data.turnover_domestic
        this.turnover_international = dataDB.data.turnover_international
        this.turnover_loading = false
        this.orders_loading = false
      },

      chip_color(event_status) {
        let color = 'primary'

        if (event_status == 'AccessGranted') {
          color = 'success'
          return color
        }

        else if (event_status == 'AccessDenied') {
          color = 'error'
          return color
        }

        else if (event_status == 'TryAgain') {
          color = 'warning'
          return color
        }
        
        else if (event_status == 'NotRegistered') {
          color = 'info'
          return color
        }

      },
    },

    mounted() {
      this.getUserData()
      this.getParcelData()
    },
  }
</script>
