   methods: {
      getTransport(value) {
        if(value == 2) {
          return 'От офис'
        }
        else {
          return 'От адрес'
        }
      },
      async getParcelDatabase() {
        const dataDB = await axios.get('/admin/parcels')
        console.log(dataDB);
        if(dataDB.data === []) {
          // console.log(dataDB.data)
          this.database_data = {
            'orderNumber': '123',
            'dateCreated': 'today',
            'createdBy': 'user_email',
            'parcelType': '1',
            'parcelDetails': {
                'weight': 1,
                'deliveryType': 'OfficeToOffice',
                'pickupLocation': 'test',
                'dropoffLocation': 'test',
            },
            'transportedBy': 'test',
            'transportOrderNumber': 'test',
            'paymentType': 'cash',
            'paymentStatus': 'complete',
            'parcelStatus': 'none'
          }
          this.data_loading = false
        }
        else {
          this.database_data = dataDB.data
          // console.log(dataDB.data)
          this.data_loading = false
        }
      },

      getColor (parcelType) {
        if (parcelType == 2) return 'primary'
        else if (parcelType == 4) return 'secondary'
        else return 'green'
      },

      getColorTransport (transportedBy) {
        if (transportedBy == 'Evropat') return 'red'
        else if (transportedBy == 'Direx') return 'dark-blue'
        else if (transportedBy == 'Econt') return 'blue'
        else return 'green'
      },

      async updateParcel(item) {
        const payload = {
          'orderNumber':item.orderNumber,
        }
        await axios.post('/admin/parcels/finish', payload)
      }

    },
    mounted(){
      this.getParcelDatabase()
    },
  }
